.sp-no-outline {
  outline: none;
}
.sp-drawer-mask {
  /* background-color: transparent !important; */
}
#sp-danmu-input {
  margin-left: 30px;
  margin-right: 10px;
  display: inline-block;
}
.sp-danmu-input-textarea {
  font-size: 1.1rem !important;
}
.margin-5 {
  margin: 5px;
}
.sp-button {
  font-size: 1.2rem;
  font-weight: 500;
  letter-spacing: .007px;
  text-transform: none !important;
  border-radius: 2px !important; 
  margin: 5px !important;
}

.sp-comment-count {
  /* font-size: 12px; */
  /* font-weight: 500; */
  margin-left: 5px;
}

#sp-danmu-bar {
  /* display: none; */
}
.sp-duonao-bar {
  margin-bottom: 35px;
}
.sp-youtube-bar {
  margin-top: 20px;
}
.sp-danmu-input-timestamp {
  line-height: 50px;
  font-size: 1.2rem;
  border-radius: 2px;
  padding: 5px;
  padding-left: 8px;
  padding-right: 8px;
}
.sp-youtube-bar .sp-danmu-input-timestamp {
  line-height: 45px;
}
.sp-danmu-input-btn-group {
  display: block;
  text-align: right;
  /* margin-top: -10px; */
}
.sp-danmu-input-not-login {
  display: block;
  text-align: right;
  color: rgb(0, 132, 255);
}
.sp-danmu-input-not-login:hover {
  text-decoration: underline;
  cursor: pointer;
}
.sp-danmu-input-sent {
  display: block;
  text-align: right;
  font-size: 1.2rem;
  /* margin-top: -10px; */
  /* color: rgb(0, 119, 255); */
}
.sp-danmu-input-sent.success {
  color: rgb(0, 132, 255);
}
.sp-danmu-input-sent.fail {
  color: red;
}

.sp-danmu-wrapper {
    display: -webkit-flex;
    display: flex;
    position: fixed;
    cursor: pointer;
    z-index: 2147483647;
}
.sp-danmu-avatar {
    /* display: inline-block; */
    border-radius: 100%;
    width: 35px;
    height: 35px;
    object-fit: cover;
    border: 1px solid lightgray;
}
.sp-danmu-content-text {
    background-color: #0095ff !important;
    color: white !important;
    padding: 5px;
    padding-left: 10px;
    padding-right: 10px;
    font-size: 18px;
    border-radius: 5px;
    max-width: 500px;
    margin-left: 5px;
    border: none;
    overflow: hidden;
    text-overflow: ellipsis;
    word-break: break-word;
    box-shadow: 7px 7px 3px rgba(0, 0, 0, 0.2);
}
.sp-danmu-content-text.self {
    background-color: #bf0 !important;
    color: black !important;
}
.sp-danmu-content-img {
    width: 100px;
    height: 100px;
    margin-top: -10px;
}
.inline-emoji {
    /* font-size: 18px; */
    /* bottom: -3px; */
    /* position: relative; */
}

.sp-chatbox-iframe {
    /* don't remove anything, this is fighting other sites' styling*/
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: 2147483647;
/*    height: 415px;
    width: 360px;*/
/*    height: 30px;
    width: 100px;*/
    height: 0px;
    width: 0px;
    min-height: 0px;
    min-width: 0px;
    border:none;
    box-shadow: 15px 15px 5px rgba(0, 0, 0, 0.31);
    display: block;
    margin-bottom: 0px;
}
